@import '../../themify.scss';
@import '../../theme.scss';
.list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    flex: 1;
    overflow-y: auto;
    margin: 0.5em 0;
    .message.own {
        align-items: flex-end;
    }
    .dummy {
        float: left;
        clear: both;
    }
    .message {
        display: flex;
        flex-direction: column;
        width: 100%;
        .content {
            display: flex;
            width: fit-content;
            max-width: 64%;
            min-width: 32%;
            border-radius: 4px;
            padding: 0.5em;
            margin: 2px 0;
            justify-content: flex-start;
            flex-direction: column;
            cursor: pointer;
            &:focus,
            &:active {
                outline: none;
            }
            @include themify($themes) {
                background-color: themed('alternativeBackgroundColor');
                color: themed('textColor');
                border: 1px solid themed('borderColor');
            }
            .header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                //flex-direction: column;
                padding-bottom: 0.5em;
                @include themify($themes) {
                    border-bottom: 1px solid themed('borderColor');
                }
                .user {
                    font-size: 0.8em;
                    text-transform: capitalize;
                    margin: 0;
                    padding: 0;
                    font-weight: bold;
                }
                .date {
                    font-size: 0.6em;
                }
            }
            .body {
                padding: 0.5em 0 0;
            }
        }
    }
}
